<template>
  <v-detail :songs="isongs" :singer="singer"></v-detail>
</template>
<script type="text/javascript">
  import {mapGetters} from 'vuex'
  import {getSingerDetail} from 'api/singer'
  import {ERR_OK} from 'api/config'
  import {createSong} from 'common/js/song'
  import VDetail from 'base/detail/detail'

  export default{
    data() {
      return {
        isongs: []
      }
    },
    mounted() {
    },
    computed: {
      ...mapGetters([
        'singer'
      ])
    },
    created() {
      this._getDetail()
    },
    methods: {
      _getDetail() {
        if (!this.singer.id) {
          this.$router.push('/singer')
          return
        }
        getSingerDetail(this.singer.id).then((res) => {
          if (res.code === ERR_OK) {
            this.isongs = this._normaSongs(res.data.list)
          }
        })
      },
      _normaSongs(list) {
        let ret = []
        list.forEach((item) => {
          let musicData = item.musicData
          if (musicData.songid && musicData.albummid) {
            ret.push(createSong(musicData))
          }
        })
        return ret
      },
      _balck() {
        this.$router.back()
      },
      _randomPlay() {
        this.randomPlay({
          list: this.isongs
        })
      }
    },
    components: {
      VDetail
    }
  }
</script>


